import React from 'react';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import SwipeableViews from 'react-swipeable-views';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { Hidden } from '@material-ui/core';

const styles = theme => ({
    root: {        
        width: 'auto',
        marginLeft: theme.spacing.unit * 3,
        marginRight: theme.spacing.unit * 3,
        [theme.breakpoints.up(1100 + theme.spacing.unit * 3 * 2)]: {
            width: 1100,
            marginLeft: 'auto',
            marginRight: 'auto',
        },
    },
    column: {
        flexBasis: '33.33%',
    },
    column2: {
        flexBasis: '66.66%',
    },    
});

const renderTimestamp = time => {    
    let timestamp = new Date(time);
    return timestamp.toLocaleString();
}

const renderStatus = status => {
    switch (status) {
        case 0:
            return (<Typography variant='button' style={{color:'red'}}>Pendiente</Typography>);
        case 1:
            return (<Typography variant='button' style={{color:'orange'}}>En Proceso</Typography>);
        case 2:
            return (<Typography variant='button' style={{color:'green'}}>Finalizado</Typography>);
        default:
            return (<Typography variant='button' style={{color:'red'}}>Pendiente revisin</Typography>);
            
    }
}

const PedidosLayout = ({ classes, theme, value, handleToggleView, handleChangeIndex, pedidos, pedidosUsuario, handlePedidoEnProceso, handlePedidoFinalizado }) => {    
    return (
        <div className={classes.root}>                
            
            <AppBar position="static" color="default">
                <Tabs
                    value={value}
                    onChange={handleToggleView}
                    indicatorColor="primary"                    
                    variant="fullWidth"
                >
                    <Tab label="Tus pedidos" />
                    <Tab label="Todos los pedidos" />                
                </Tabs>
            </AppBar>

            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={value}
                onChangeIndex={handleChangeIndex}
            >    
                <Typography component="div" dir={theme.direction} style={{ padding: 8 * 3 }}>
                    
                
                    { 
                        (pedidosUsuario.length > 0) ?
                            <Typography variant="h4" align='center' gutterBottom component="h2">
                                Listado de tus pedidos
                            </Typography>        
                        :
                            <Typography variant="h6" align='center' gutterBottom component="p">
                                No tens ningn pedido realizado por el momento.
                            </Typography>
                    }    

                    {                
                        pedidosUsuario.map((pedido) =>            
                        <ExpansionPanel key={pedido.id}>
                            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                                <div style={{flexBasis: '33%'}}>
                                    {renderStatus(pedido.status)}
                                </div>
                                <div style={{flexBasis: '66%', paddingLeft: 16}}>
                                    <Typography variant='caption'>{renderTimestamp(pedido.timestamp)}</Typography>
                                    
                                    <Hidden only={['xs']}>
                                        <Typography variant='h6' style={{fontSize: 14}}>
                                            {`Total $${pedido.montoTotal}`}
                                            {` (${pedido.unidadesCarrito}`}
                                            {pedido.unidadesCarrito > 1 ? ' unidades)' : ' unidad)'}
                                        </Typography>    
                                    </Hidden>
                                    
                                    <Hidden smUp>
                                        <Typography variant='h6' style={{fontSize: 14}}>
                                            {`Total $${pedido.montoTotal}`}
                                        </Typography>    
                                        <Typography variant='h6' style={{fontSize: 12}}>
                                            {`(${pedido.unidadesCarrito}`}
                                            {pedido.unidadesCarrito > 1 ? ' unidades)' : ' unidad)'}
                                        </Typography>                                                                                                        
                                    </Hidden>

                                </div>                                
                            </ExpansionPanelSummary>
                            <ExpansionPanelDetails>     
                                <Grid container>                                                                   
                                    <Grid item xs={12} sm={6}>
                                        <Typography variant="h6" gutterBottom>
                                            Carrito
                                        </Typography>
                                        
                                        {pedido.carrito && pedido.carrito.map(item => (                                        
                                            <Typography key={item.nombre} variant='body1' gutterBottom>{`${item.cantidad} x ${item.nombre}`}</Typography>
                                        ))}
                                        <Typography variant='caption' paragraph style={{marginTop: 4}}>
                                            {`Total $${pedido.montoTotal} (${pedido.unidadesCarrito}`}
                                            { pedido.unidadesCarrito > 1 ? ' unidades)' : ' unidad)'}
                                        </Typography>
                                        
                                        <Typography variant="h6" gutterBottom>
                                            Punto de entrega
                                        </Typography>
                                        <Grid container item>
                                            <Grid item xs={6}>
                                                <Typography gutterBottom variant='caption'>Dia</Typography>
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Typography variant='body1' gutterBottom>{pedido.entregaDia}</Typography>
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Typography gutterBottom variant='caption'>Hora</Typography>
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Typography variant='body1' gutterBottom>{pedido.entregaHora}</Typography>
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Typography gutterBottom variant='caption'>Direccin</Typography>
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Typography variant='body1' gutterBottom>{pedido.entregaDireccion}</Typography>
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Typography gutterBottom variant='caption'>Recibe</Typography>
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Typography variant='body1' gutterBottom>{pedido.entregaReceptor}</Typography>
                                            </Grid>            
                                        </Grid>
                                    </Grid>
                                </Grid>                                              
                            </ExpansionPanelDetails>                            
                        </ExpansionPanel>                            
                        )            
                    }



                </Typography>


                <Typography component="div" dir={theme.direction} style={{ padding: 8 * 3 }}>
                    
                    { 
                        (pedidos.length > 0) ?
                            <Typography variant="h4" align='center' gutterBottom component="h2">
                                Listado de todos los pedidos
                            </Typography>        
                        :
                            <Typography variant="h6" align='center' gutterBottom component="p">
                                No se registr ningn pedido por el momento.
                            </Typography>
                    }

                    {
                        pedidos.reverse().map(
                            (pedido) =>            
                            <ExpansionPanel key={pedido.id}>
                                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                                    <div style={{flexBasis: '33%'}}>
                                        {renderStatus(pedido.status)}
                                    </div>
                                    <div style={{flexBasis: '64%', paddingLeft: 16}}>
                                        <Typography variant='caption'>{renderTimestamp(pedido.timestamp)}</Typography>
                                        <Typography variant='h6' style={{fontSize:16}}>{pedido.name}</Typography>
                                    </div>
                                    
                                </ExpansionPanelSummary>
                                <ExpansionPanelDetails>     
                                    <Grid container>                               
                                        <Grid item xs={12} sm={6}>
                                            <Typography variant="h6" gutterBottom>
                                                Cliente
                                            </Typography>
                                            <Typography variant='body1' gutterBottom>{pedido.name}</Typography>
                                            <Typography variant='body1' gutterBottom>{pedido.email}</Typography>
                                            <Typography variant='body1' paragraph>{pedido.tel}</Typography>
                                            
                                            <Typography variant="h6" gutterBottom>
                                                Carrito
                                            </Typography>
                                            
                                            {pedido.carrito && pedido.carrito.map(item => (                                        
                                                <Typography key={item.nombre} variant='body1' gutterBottom>{`${item.cantidad} x ${item.nombre}`}</Typography>
                                            ))}
                                            <Typography variant='h6' paragraph style={{marginTop: 8, fontSize: 16}}>
                                                {`Total $${pedido.montoTotal} (${pedido.unidadesCarrito}`}
                                                { pedido.unidadesCarrito > 1 ? ' unidades)' : ' unidad)'}
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <Typography variant="h6" gutterBottom>
                                                Entrega
                                            </Typography>
                                            <Grid container item>
                                                <Grid item xs={6}>
                                                    <Typography gutterBottom variant='caption'>Dia</Typography>
                                                </Grid>
                                                <Grid item xs={6}>
                                                    <Typography variant='body1' gutterBottom>{pedido.entregaDia}</Typography>
                                                </Grid>
                                                <Grid item xs={6}>
                                                    <Typography gutterBottom variant='caption'>Hora</Typography>
                                                </Grid>
                                                <Grid item xs={6}>
                                                    <Typography variant='body1' gutterBottom>{pedido.entregaHora}</Typography>
                                                </Grid>
                                                <Grid item xs={6}>
                                                    <Typography gutterBottom variant='caption'>Direccin</Typography>
                                                </Grid>
                                                <Grid item xs={6}>
                                                    <Typography variant='body1' gutterBottom>{pedido.entregaDireccion}</Typography>
                                                </Grid>
                                                <Grid item xs={6}>
                                                    <Typography gutterBottom variant='caption'>Recibe</Typography>
                                                </Grid>
                                                <Grid item xs={6}>
                                                    <Typography variant='body1' gutterBottom>{pedido.entregaReceptor}</Typography>
                                                </Grid>            
                                            </Grid>
                                        </Grid>
                                    </Grid>                                              
                                </ExpansionPanelDetails>
                                <Divider />
                                <ExpansionPanelActions>
                                    {
                                        (pedido.status === 0) &&
                                        <Button 
                                            onClick={() => handlePedidoEnProceso(pedido.id, 'mensaje 1')}
                                            size="small" variant='contained' color="secondary"
                                        >Envo en Proceso</Button>
                                    }
                                    {
                                        (pedido.status === 0 || pedido.status === 1) &&
                                        <Button 
                                            onClick={() => handlePedidoFinalizado(pedido.id, 'mensaje 2')}
                                            size="small" variant='contained' color="primary"
                                        >Entregado</Button>
                                    }        
                                    {
                                        (pedido.status === 2) &&
                                        <Typography variant='h6' style={{fontSize: 16, color:'green'}}>
                                            Pedido finalizado
                                        </Typography>
                                    }                            
                                </ExpansionPanelActions>
                            </ExpansionPanel>                  
                        )  
                    }

                </Typography>                                
            </SwipeableViews>
        
        
        </div>
    );
};

PedidosLayout.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
    value: PropTypes.number.isRequired,
    handleToggleView: PropTypes.func.isRequired,
    handleChangeIndex: PropTypes.func.isRequired,
    pedidos: PropTypes.array.isRequired,
    pedidosUsuario: PropTypes.array.isRequired,
    handlePedidoEnProceso: PropTypes.func.isRequired,
    handlePedidoFinalizado: PropTypes.func.isRequired,
  };
  
  export default withStyles(styles, { withTheme: true })(PedidosLayout);