import React from 'react';
import PropTypes from 'prop-types';

import Paper from '@material-ui/core/Paper';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import InputAdornment from '@material-ui/core/InputAdornment';
import Grid from '@material-ui/core/Grid';
import Chip from '@material-ui/core/Chip';
import LinearProgress from '@material-ui/core/LinearProgress';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';

import { Formik, Field, Form } from 'formik';
import { TextField, RadioGroup } from 'formik-material-ui';

import FormReview from './FormReview';
import CarritoVacio from '../Carrito/CarritoVacio';
import Header from '../Home/Header';
import { getMontoCarrito, getUnidadesCarrito } from '../../helpers/carritoHelpers';

import withStyles from '@material-ui/core/styles/withStyles';

import Fade from 'react-reveal/Fade';

const styles = theme => ({    
    paper: {        
        margin: '0 auto', 
        marginTop: 36,
        marginBottom: 48,
        padding: `${theme.spacing.unit * 2.5}px ${theme.spacing.unit * 4}px`,        
        [theme.breakpoints.up(600 + theme.spacing.unit * 3 * 2)]: {
            width: 600,                    
            padding: `${theme.spacing.unit * 3}px ${theme.spacing.unit * 8}px`,
        },
    },
    stepper: {
      padding: `${theme.spacing.unit * 3}px 0 ${theme.spacing.unit * 5}px`,
    },
    buttons: {
      display: 'flex',
      margin: '0 auto',
      marginTop: 8,
      marginBottom: 16,
      justifyContent: 'center',      
    },
    button: {
      marginTop: theme.spacing.unit * 3,
      marginLeft: theme.spacing.unit,
    },
  });
  
const steps = ['Tus datos', 'Da y lugar de entrega', 'Revis tu pedido'];

const calcularMes = () => {
    let mes = new Date().getMonth();    
    switch (mes) {
        case 0: return 'Enero'
        case 1: return 'Febrero'
        case 2: return 'Marzo'
        case 3: return 'Abril'
        case 4: return 'Mayo'
        case 5: return 'Junio'
        case 6: return 'Julio'
        case 7: return 'Agosto'
        case 8: return 'Septiembre'
        case 9: return 'Octubre'
        case 10: return 'Noviembre'
        case 11: return 'Diciembre'
        default: return ''
    }
}

const calcularDiaSemana = dia => {
    let tomorrow = new Date();
        
    tomorrow.setDate(tomorrow.getDate() + dia + 1);
    let diaSemana = tomorrow.getDay();
    let diaNumero = tomorrow.getDate();

    switch (diaSemana) {
        case 0: return `Domingo ${diaNumero}`
        case 1: return `Lunes ${diaNumero}`
        case 2: return `Martes ${diaNumero}`
        case 3: return `Mircoles ${diaNumero}`
        case 4: return `Jueves ${diaNumero}`
        case 5: return `Viernes ${diaNumero}`
        case 6: return `Sbado ${diaNumero}`
    
        default: return `${diaNumero} - ${calcularMes()}`
    }    
}

/* Si el pedido se hace con un cambio de da, puede haber inconsistencias. 
Debera persistirse la primera fecha en dicho caso */
const renderEntrega = (delay) => {    
    let timestamp = new Date();
    timestamp.setDate(timestamp.getDate() + parseInt(delay));  
    return timestamp.toLocaleDateString("es-ES",{weekday: "long", month: "long", day: "numeric"});
}

const isRequired = value => {
    let error;
    if (!value) {
        error = 'Campo requerido';
    } 
    return error;
};

const validateEmail = value => {
    let error;
    if (!value) {
        error = 'Campo requerido';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
        error = 'Direccin de email invlida';
    }
    return error;
};

const validateTel = value => {
    let error;
    if (!value) {
        error = 'Campo requerido';
    } else if (!/^\d*$/.test(value)) {
        error = 'Nmero de telfono incorrecto';
    } else if (value < 10000 || value > 999999999999) {
        error = 'Nmero de telfono incorrecto';
    }
    return error;
};

const validarPaso1 = (values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched) => {            
    if (validationStep === 0) handleNextValidationStep();    
    
    setFieldTouched('name', true);
    setFieldTouched('email', true);
    setFieldTouched('tel', true);

    if (isValid || (values.name !== '' && values.email !== '' && values.tel !== '' && !errors.name && !errors.email && !errors.tel) ) {
        handleNext();
        window.scrollTo({top: 0,left: 0, behavior: 'smooth'});
    }
}

const validarPaso2 = (values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched) => {            
    if (validationStep === 1) handleNextValidationStep();
    
    setFieldTouched('entregaDia', true);
    setFieldTouched('entregaHora', true);
    setFieldTouched('entregaDireccion', true);
    setFieldTouched('entregaReceptor', true);

    if (!values.entregaDia) {
        scrollToDiasSelect();
    } else if (!values.entregaHora) {
        scrollToHorasSelect();
    } else if (isValid || 
    (values.entregaHora !== '' && values.entregaDia !== '' && values.entregaDireccion !== '' && values.entregaReceptor !== '' &&
    !errors.entregaHora && !errors.entregaDia && !errors.entregaDireccion && !errors.entregaReceptor) ) {
        handleNext();
        window.scrollTo({top: 0,left: 0, behavior: 'smooth'});
    }
}

const scrollToDiasSelect = () => {    
    let element = document.getElementById("diasSelect");   
    element.scrollIntoView({block: "start", behavior: "smooth"});
}

const scrollToHorasSelect = () => {    
    let element = document.getElementById("horasSelect");   
    element.scrollIntoView({block: "start", behavior: "smooth"});
}


const CheckoutLayout = ({ classes, activeStep, validationStep, carrito, 
    handleNext, handleBack, handleNextValidationStep, handleSubmit,
    usuario, direcciones, handleRemoverDireccion, handleHomeClick
}) => {
    return (
        <React.Fragment>            

            <Header 
                handleHomeClick={handleHomeClick}         
                style={{position:'static'}}  
                fixed={false}                     
                homeSeleccionable={true}
                exitConfirmation={true}  
                carritoVisible={false}
                carritoSeleccionable={false}              
                unidades={getUnidadesCarrito(carrito)}
            />                         
              
            <Grid container justify="center" alignItems="center">
                <Grid item xs={12} style={{marginRight: 16, marginLeft: 16}}>
                    <Fade>
                        <Paper className={classes.paper}>
                            
                            <Typography component="h2" variant="h4" align="center">
                                Checkout
                            </Typography>
                            
                            { 
                                (carrito.length === 0) &&
                                <CarritoVacio 
                                    handleHomeClick={handleHomeClick}
                                />
                            }
                            
                            { 
                                (carrito.length > 0) &&
                                <React.Fragment>
                                                                    
                                    <Stepper activeStep={activeStep} className={classes.stepper}>
                                        {
                                            steps.map(label => (
                                                <Step key={label}>
                                                    <StepLabel>{label}</StepLabel>
                                                </Step>
                                            ))
                                        }
                                    </Stepper>

                                    <Formik
                                        initialValues=
                                            {{ name: usuario.name, email: usuario.email, tel: usuario.tel, entregaDia: '', entregaHora: '', entregaDireccion: '', entregaReceptor: '' }}
                                                                            
                                        onSubmit={(values, { setSubmitting }) => {                                                                                                                                                                                
                                            let newCarrito = carrito.reduce( (acc, item) => {
                                                return [ ...acc, {id: item.id, nombre: item.nombre, cantidad: item.cantidad, precio: item.precio }]
                                            }, []);
                                            let newPedido = {
                                                ...values,
                                                status: 0,
                                                mensaje: '',
                                                montoTotal: getMontoCarrito(carrito),
                                                unidadesCarrito: getUnidadesCarrito(carrito),
                                                carrito: newCarrito                                            
                                            };                                                                                
                                            handleSubmit(newPedido);
                                            setSubmitting(false);                                                        
                                        }}
                                        render={({ values, isValid, errors, status, isSubmitting, setFieldTouched, setFieldValue }) => (
                                            <Form>                            
                                                {
                                                    activeStep === 0 && (
                                                        <Grid container spacing={24}>
                                                            <Grid item xs={12}>                                                                      
                                                                <Field                                                                 
                                                                    onKeyPress={ 
                                                                        (e) => {
                                                                            if (e.key === 'Enter') {                                                                            
                                                                                validarPaso1(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                                                                            }
                                                                        }
                                                                    }
                                                                    fullWidth type="text" component={TextField} name="name" 
                                                                    validate={isRequired} label="Tu nombre"
                                                                />                                                                                                                                                    
                                                            </Grid>

                                                            <Grid item xs={12}>                                                                
                                                                <Field 
                                                                    onKeyPress={ 
                                                                        (e) => {
                                                                            if (e.key === 'Enter') {                                                                            
                                                                                validarPaso1(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                                                                            }
                                                                        }
                                                                    }                                                                
                                                                    fullWidth type="tel" minLength='5' maxLength='12' component={TextField} 
                                                                    name="tel" validate={validateTel} 
                                                                    label="Nmero de celular" placeholder="Nmero de celular"
                                                                    InputProps={{
                                                                        startAdornment: <InputAdornment position="start">(15) </InputAdornment>,
                                                                    }}
                                                                />                                                                
                                                            </Grid>

                                                            <Grid item xs={12}>                                                                
                                                                <Field 
                                                                    onKeyPress={ 
                                                                        (e) => {
                                                                            if (e.key === 'Enter') {                                                                            
                                                                                validarPaso1(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                                                                            }
                                                                        }
                                                                    }
                                                                    fullWidth type="email" component={TextField} name="email" 
                                                                    validate={validateEmail} label="Correo electrnico"                                                                
                                                                />                                                                                                            
                                                            </Grid>
                                                                                                                    
                                                            <div className={classes.buttons}>
                                                                <Button
                                                                    variant="contained"
                                                                    size='large'
                                                                    color="primary"
                                                                    onClick={() => validarPaso1(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)}                                            
                                                                    className={classes.button}
                                                                >
                                                                    Continuar
                                                                </Button>
                                                            </div>
                                                        </Grid>
                                                    )
                                                }
                                                {
                                                    activeStep === 1 && (
                                                        <Grid container spacing={24}>                                                                                                        
                                                            <Grid item xs={12}>
                                                                { 
                                                                    (validationStep === 2) && errors.entregaDia ?
                                                                        <Typography id='diasSelect' variant="subtitle2" align='center' gutterBottom style={{fontSize: 20, color: 'red'}}>
                                                                            Eleg un da de entrega...
                                                                        </Typography>
                                                                    :
                                                                        <Typography id='diasSelect' variant="subtitle2" align='center' gutterBottom style={{fontSize: 20}}>
                                                                            Eleg un da de entrega...
                                                                        </Typography>
                                                                }                                                            
                                                                <Field validate={isRequired} name="entregaDia" component={RadioGroup} style={{flexDirection:'row', alignItems: 'center', justifyContent: 'center'}}>
                                                                    <FormControlLabel
                                                                        value='1'
                                                                        control=
                                                                        {
                                                                            <Radio
                                                                                onClick={scrollToHorasSelect} 
                                                                                icon={ 
                                                                                    <Button 
                                                                                        style={{
                                                                                            textTransform:'none', marginBottom:-12, padding:'12px 16px',
                                                                                            color: (validationStep === 2) && errors.entregaDia ? 'red' :'',
                                                                                            borderColor: (validationStep === 2) && errors.entregaDia ? 'red' :''
                                                                                        }} 
                                                                                    variant="outlined">{calcularDiaSemana(0)}</Button>                                                                                
                                                                                }
                                                                                checkedIcon={                                                                                
                                                                                    <Button style={{textTransform:'none', marginBottom:-12, padding:'12px 16px'}} variant="contained" color='primary'>{calcularDiaSemana(0)}</Button>                                                                                
                                                                                }                                                                            
                                                                            />
                                                                        }
                                                                    />
                                                                    <FormControlLabel
                                                                        value='2'
                                                                        control=
                                                                        {
                                                                            <Radio
                                                                                onClick={scrollToHorasSelect} 
                                                                                icon={ 
                                                                                    <Button 
                                                                                        style={{
                                                                                            textTransform:'none', marginBottom:-12, padding:'12px 16px',
                                                                                            color: (validationStep === 2) && errors.entregaDia ? 'red' :'',
                                                                                            borderColor: (validationStep === 2) && errors.entregaDia ? 'red' :''
                                                                                        }}
                                                                                    variant="outlined">{calcularDiaSemana(1)}</Button>                                                                                
                                                                                }
                                                                                checkedIcon={                                                                                
                                                                                    <Button style={{textTransform:'none', marginBottom:-12, padding:'12px 16px'}} variant="contained" color='primary'>{calcularDiaSemana(1)}</Button>                                                                                
                                                                                }                                                                            
                                                                            />
                                                                        }
                                                                    />
                                                                    <FormControlLabel
                                                                        value='3'
                                                                        control=
                                                                        {
                                                                            <Radio
                                                                                onClick={scrollToHorasSelect} 
                                                                                icon={ 
                                                                                    <Button 
                                                                                        style={{
                                                                                            textTransform:'none', marginBottom:-12, padding:'12px 16px',
                                                                                            color: (validationStep === 2) && errors.entregaDia ? 'red' :'',
                                                                                            borderColor: (validationStep === 2) && errors.entregaDia ? 'red' :''
                                                                                        }}
                                                                                    variant="outlined">{calcularDiaSemana(2)}</Button>                                                                                
                                                                                }
                                                                                checkedIcon={                                                                                
                                                                                    <Button style={{textTransform:'none', marginBottom:-12, padding:'12px 16px'}} variant="contained" color='primary'>{calcularDiaSemana(2)}</Button>                                                                                
                                                                                }                                                                            
                                                                            />
                                                                        }
                                                                    />
                                                                </Field>                                                            
                                                            </Grid>
                                                        
                                                            <Grid id='horasSelect' item xs={12} style={{marginTop: 16, marginBottom: 32}}>
                                                                {
                                                                    (validationStep === 2) && errors.entregaHora ?
                                                                        <Typography variant="subtitle2" align='center' gutterBottom style={{fontSize: 20, color: 'red'}}>
                                                                            ...y un rango horario de entrega.
                                                                        </Typography>
                                                                    :
                                                                        <Typography variant="subtitle2" align='center' gutterBottom style={{fontSize: 20}}>
                                                                            ...y un rango horario de entrega.
                                                                        </Typography>
                                                                }

                                                                <Field name="entregaHora" validate={isRequired} component={RadioGroup} style={{flexDirection:'row', alignItems: 'center', justifyContent: 'center'}}>                                                                
                                                                    <FormControlLabel
                                                                        value="1"
                                                                        control=
                                                                        {
                                                                            <Radio 
                                                                                icon={ 
                                                                                    <Button 
                                                                                        style={{
                                                                                            textTransform:'none', marginBottom:-12, padding:'12px 16px',
                                                                                            color: (validationStep === 2) && errors.entregaHora ? 'red' :'',
                                                                                            borderColor: (validationStep === 2) && errors.entregaHora ? 'red' :''
                                                                                        }}
                                                                                    variant="outlined">8:00 a 12:00 hs</Button>                                                                                
                                                                                }
                                                                                checkedIcon={
                                                                                    
                                                                                    <Button style={{textTransform:'none', marginBottom:-12, padding:'12px 16px'}} variant="contained" color='primary'>8:00 a 12:00 hs</Button>                                                                                
                                                                                }
                                                                                
                                                                            />
                                                                        }                                                                    
                                                                    />
                                                                    <FormControlLabel
                                                                        value="2"
                                                                        control=
                                                                        {
                                                                            <Radio 
                                                                                icon={ 
                                                                                    <Button
                                                                                        style={{
                                                                                            textTransform:'none', marginBottom:-12, padding:'12px 16px',
                                                                                            color: (validationStep === 2) && errors.entregaHora ? 'red' :'',
                                                                                            borderColor: (validationStep === 2) && errors.entregaHora ? 'red' :''
                                                                                        }}
                                                                                    variant="outlined">12:00 a 16:00 hs</Button>                                                                                
                                                                                }
                                                                                checkedIcon={
                                                                                    
                                                                                    <Button style={{textTransform:'none', marginBottom:-12, padding:'12px 16px'}} variant="contained" color='primary'>12:00 a 16:00 hs</Button>                                                                                
                                                                                }
                                                                                
                                                                            />
                                                                        }                                                                    
                                                                    />
                                                                    <FormControlLabel
                                                                        value="3"
                                                                        control=
                                                                        {
                                                                            <Radio 
                                                                                icon={ 
                                                                                    <Button 
                                                                                        style={{
                                                                                            textTransform:'none', marginBottom:-12, padding:'12px 16px',
                                                                                            color: (validationStep === 2) && errors.entregaHora ? 'red' :'',
                                                                                            borderColor: (validationStep === 2) && errors.entregaHora ? 'red' :''
                                                                                        }}
                                                                                    variant="outlined">16:00 a 20:00 hs</Button>                                                                                
                                                                                }
                                                                                checkedIcon={
                                                                                    
                                                                                    <Button style={{textTransform:'none', marginBottom:-12, padding:'12px 16px'}} variant="contained" color='primary'>16:00 a 20:00 hs</Button>                                                                                
                                                                                }
                                                                                
                                                                            />
                                                                        }                                                                    
                                                                    />
                                                                </Field>
                                                            </Grid>

                                                            { direcciones.length > 0 &&                                                        
                                                                <Grid item container xs={12}>
                                                                    <Grid item xs={12} sm={4}>
                                                                        <Typography variant="caption" align='center'>
                                                                            Direcciones utilizadas anteriormente:
                                                                        </Typography>
                                                                    </Grid>
                                                                    <Grid item container xs={12} sm={8}>
                                                                        {
                                                                            direcciones.map((direccion, index) => (
                                                                                <Grid item key={index}>                                                                            
                                                                                    <Chip                                                                                                                        
                                                                                        label={direccion}
                                                                                        onClick={() => setFieldValue('entregaDireccion', direccion)}
                                                                                        onDelete={() => handleRemoverDireccion(direccion)} 
                                                                                        style={{margin: '0 4px'}}  
                                                                                        color={direccion === values.entregaDireccion ? 'primary' : 'default'} 
                                                                                    />
                                                                                </Grid>
                                                                            ))
                                                                        }
                                                                    </Grid>

                                                                </Grid>                                                        
                                                            }
                                                            
                                                            
                                                            
                                                            <Grid item xs={12}>
                                                                <Field 
                                                                    onKeyPress={ 
                                                                        (e) => {
                                                                            if (e.key === 'Enter') {                                                                            
                                                                                validarPaso2(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                                                                            }
                                                                        }
                                                                    }
                                                                    fullWidth type="text" component={TextField} name="entregaDireccion" 
                                                                    validate={isRequired} label="Direccin de entrega"
                                                                />
                                                            </Grid>
                                                            
                                                            <Grid item xs={12}>
                                                                <Field 
                                                                    onKeyPress={ 
                                                                        (e) => {
                                                                            if (e.key === 'Enter') {                                                                            
                                                                                validarPaso2(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                                                                            }
                                                                        }
                                                                    }
                                                                    fullWidth type="text" component={TextField} name="entregaReceptor" 
                                                                    helperText="Indicanos si otra persona va a recibir el pedido" label="Nombre de la persona que lo recibe"
                                                                />
                                                            </Grid>

                                                            <div className={classes.buttons}>                                                                                        
                                                                <Button size='small' onClick={() => handleBack()} className={classes.button}>
                                                                    volver atrs
                                                                </Button>                                                
                                                                <Button
                                                                    size='large'
                                                                    variant="contained"
                                                                    color="primary"
                                                                    onClick={() => validarPaso2(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)}                                            
                                                                    className={classes.button}
                                                                >
                                                                    Continuar
                                                                </Button>
                                                            </div>                                        
                                                        </Grid>                                          
                                                    )
                                                }
                                                {
                                                    activeStep === 2 && (
                                                        <React.Fragment>
                                                            <FormReview 
                                                                values={values} 
                                                                carrito={carrito} 
                                                                montoCarrito={getMontoCarrito(carrito)}
                                                                unidadesCarrito={getUnidadesCarrito(carrito)} 
                                                                fechaEntrega={renderEntrega(values.entregaDia)}
                                                            />      
                                                            {status && status.msg && <div>{status.msg}</div>}

                                                            {isSubmitting && <LinearProgress />}

                                                            <div className={classes.buttons}>                                                                                        
                                                                <Button size='small' onClick={() => handleBack()} className={classes.button}>
                                                                    Atrs
                                                                </Button>                                                
                                                                <Button                                                
                                                                    size='large'
                                                                    type="submit"
                                                                    disabled={isSubmitting}
                                                                    variant="contained"
                                                                    color="primary"                                                
                                                                    className={classes.button}
                                                                >
                                                                    Confirmar pedido
                                                                </Button>
                                                            </div>   
                                                                                                                            
                                                        </React.Fragment>
                                                    )
                                                }                            
                                                                        
                                            </Form>
                                        )}
                                    />
                                </React.Fragment>
                            }            
                        </Paper>
                    </Fade>
                </Grid>
            </Grid>
            
        </React.Fragment>
    );
};

CheckoutLayout.propTypes = {
    activeStep: PropTypes.number.isRequired,
    validationStep: PropTypes.number.isRequired,
    carrito: PropTypes.array.isRequired,
    handleNext: PropTypes.func.isRequired,
    handleBack: PropTypes.func.isRequired,
    handleNextValidationStep: PropTypes.func.isRequired,
    handleSubmit: PropTypes.func.isRequired,    
    usuario: PropTypes.object.isRequired,
    direcciones: PropTypes.array.isRequired,    
    handleRemoverDireccion: PropTypes.func.isRequired,
    handleHomeClick: PropTypes.func.isRequired,
};

export default withStyles(styles)(CheckoutLayout);