import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { connect } from 'react-redux';
import { getPedidos, getPedidosUsuario } from '../redux/selectors';
import { fetchPedidos, getPedido, updatePedido } from "../redux/actions";

import PedidosLayout from '../components/Pedidos/PedidosLayout';

class PedidosContainer extends Component {    
    /* 
        0: VISTA DE PEDIDOS DEL USUARIO
        1: VISTA DE TODOS LOS PEDIDOS
     */
    state = {value: 0};
    
    handleToggleView = (event, value) => {
        this.setState({value});
    };
    handleChangeIndex = index => {
        this.setState({ value: index });
    };

    constructor(props) {
        super(props);        
        this.props.fetchPedidos();                
        this.props.pedidosUsuario.map(pedido =>  this.props.getPedido(pedido.id));                          
    }

    componentDidMount() {        
        window.scrollTo({top: 0,left: 0, behavior: 'smooth'});
    }

    handlePedidoEnProceso = (updatePedidoId, mensaje) => {        
        let updates= { status: 1, mensaje: mensaje }
        this.props.updatePedido(updatePedidoId, updates);
    };

    handlePedidoFinalizado = (updatePedidoId, mensaje) => {        
        let updates= { status: 2, mensaje: mensaje }
        this.props.updatePedido(updatePedidoId, updates);
    };
    
    
    render() {
        const { pedidos, pedidosUsuario } = this.props;
        return (
            <PedidosLayout
                value={this.state.value}
                handleToggleView={this.handleToggleView}
                handleChangeIndex={this.handleChangeIndex}
                pedidos={pedidos}
                pedidosUsuario={pedidosUsuario}                
                handlePedidoEnProceso={this.handlePedidoEnProceso}                
                handlePedidoFinalizado={this.handlePedidoFinalizado}                
            />            
        );
    }
}

PedidosContainer.propTypes = {
    pedidos: PropTypes.array.isRequired,
    pedidosUsuario: PropTypes.array.isRequired,
    fetchPedidos: PropTypes.func.isRequired,
    getPedido: PropTypes.func.isRequired,    
    updatePedido: PropTypes.func.isRequired, 
};

const mapStateToProps = (state) => ({ pedidos: getPedidos(state), pedidosUsuario: getPedidosUsuario(state) });
const mapDispatchToProps = { fetchPedidos, getPedido, updatePedido };
export default connect(mapStateToProps, mapDispatchToProps)(PedidosContainer);